@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 80%;
  margin: 0 auto;
  border-bottom: 1px solid #45626842;
}

.navbar h1{
  color: #01151a;
  margin-right: auto;
}

.navbar.links{
  margin-left: auto;
}

.navbar a{
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar a:hover{
  color: #000000;
}

.content{
  max-width: 80%;
  margin: 40px auto;
  padding: 20px;
  border-left: 1px solid #01171d38;;
}

.blog-preview{
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
  border-radius: 5px;
  background-color: #456268f6;
}

.blog-preview:hover{
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.966);
}

.blog-preview h2{
  font-size: 20px;
  color: #f3f3f3;
  margin-bottom: 8px;
}

.blog-preview p{
  color: #f3f3f3;
}

.blog-preview a{
  text-decoration: none;
}
